"use client";

import { useEffect, useRef, useState } from "react";
import { motion, useAnimation, useInView } from "framer-motion";

/* ============================================
   ANIMATION CONFIGURATION
   ============================================ */

const ANIMATION_CONFIG = {
  CONTAINER: {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: { staggerChildren: 0.08, when: "beforeChildren" },
    },
  },
  ITEM: {
    hidden: { opacity: 0, y: 18 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.55, ease: "easeOut" },
    },
  },
  WAVE: { DURATION: 12 },
  GLOW_TEXT: { DURATION: 3, REPEAT: Infinity, EASE: "easeInOut" },
  BLOB: { DURATIONS: [6.5, 7.5, 7] },
};

const prefersReducedMotion = () =>
  typeof window !== "undefined" &&
  window.matchMedia("(prefers-reduced-motion: reduce)").matches;

/* ============================================
   CARD DATA
   ============================================ */

const CARDS_DATA = [
  {
    id: "rumahan",
    title: "Rasa Autentik",
    desc: "Cita rasa rumahan Bandung yang hangat dan bikin kangen.",
    img: "/assets/about/t1.jpg",
  },
  {
    id: "lokal",
    title: "Dari Komunitas Lokal",
    desc: "Kuliner yang tumbuh dari komunitas Gelap Nyawang sendiri.",
    img: "/assets/about/t2.jpg",
  },
  {
    id: "cepat",
    title: "Cepat & Nyaman",
    desc: "Tempat singgah yang pas buat makan cepat atau rehat sebentar.",
    img: "/assets/about/t3.jpg",
  },
];

const IMAGE_EFFECT = { brightness: 0.78 };

/* ============================================
   CARD COMPONENT
   ============================================ */

const AboutCard = ({ card, isMobile }) => {
  const reduceMotion = prefersReducedMotion();

  return (
    <motion.article
      variants={ANIMATION_CONFIG.ITEM}
      whileHover={!isMobile ? { scale: 1.03, translateY: -4 } : {}}
      transition={{
        type: "spring",
        stiffness: 140,
        damping: 18,
        ...(reduceMotion && { duration: 0.15 }),
      }}
      className="relative rounded-2xl overflow-hidden shadow-card bg-bg-soft dark:bg-bg-base transition-all duration-300 group"
    >
      {/* BACKGROUND IMAGE */}
      <div
        className="w-full h-[320px] sm:h-[360px] md:h-[420px]
        bg-cover bg-center transition-all duration-300 group-hover:brightness-110"
        style={{
          backgroundImage: `url(${card.img})`,
          filter: `brightness(${IMAGE_EFFECT.brightness})`,
        }}
      />

      {/* GRADIENT (dipendekin sesuai permintaan) */}
      <div
        className="
          absolute inset-x-0 bottom-0 
          h-[38%] 
          bg-gradient-to-t
          from-primary/90 via-primary-light/75 to-transparent
          dark:from-accent/90 dark:via-accent-light/75 dark:to-transparent
          flex flex-col justify-end p-6 backdrop-blur-[2px]"
      >
        {/* TITLE WITH WARM PREMIUM GLOW */}
        <motion.h3
          className="text-2xl md:text-3xl font-semibold text-white drop-shadow-xl"
          animate={{
            textShadow: [
              "0 0 0px rgba(229,118,33,0.0)",
              "0 0 12px rgba(229,118,33,0.55)",
              "0 0 0px rgba(229,118,33,0.0)",
            ],
          }}
          transition={{
            duration: 2.2,
            repeat: Infinity,
            ease: "easeInOut",
          }}
        >
          {card.title}
        </motion.h3>

        <p className="text-sm md:text-base text-white/90 mb-0 line-clamp-2">
          {card.desc}
        </p>
      </div>
    </motion.article>
  );
};

/* ============================================
   MAIN SECTION
   ============================================ */

export default function Tentang() {
  const ref = useRef(null);
  const inView = useInView(ref, { amount: 0.25, once: true });
  const controls = useAnimation();

  const [isMobile, setIsMobile] = useState(false);
  const [reduceMotion, setReduceMotion] = useState(false);

  useEffect(() => {
    const check = () => setIsMobile(window.innerWidth < 640);
    check();
    window.addEventListener("resize", check);
    return () => window.removeEventListener("resize", check);
  }, []);

  useEffect(() => {
    const mq = window.matchMedia("(prefers-reduced-motion: reduce)");
    setReduceMotion(mq.matches);
    mq.addEventListener("change", (e) => setReduceMotion(e.matches));
    return () => mq.removeEventListener("change", () => {});
  }, []);

  useEffect(() => {
    if (reduceMotion) {
      controls.set("visible");
    } else if (inView) {
      controls.start("visible");
    }
  }, [inView, reduceMotion]);

  return (
    <section
      id="tentang"
      ref={ref}
      className="relative overflow-hidden transition-colors duration-500"
    >
      {/* WAVE TOP */}
      <div className="absolute inset-x-0 -top-[2px] z-20 overflow-hidden pointer-events-none">
        <motion.div
          animate={{ x: ["0%", "-18%", "0%"] }}
          transition={{
            duration: reduceMotion ? 0.1 : ANIMATION_CONFIG.WAVE.DURATION,
            repeat: Infinity,
            ease: "easeInOut",
          }}
          className="w-[200%]"
        >
          <svg
            viewBox="0 0 1440 80"
            preserveAspectRatio="none"
            className="w-full h-[80px]"
          >
            <defs>
              <linearGradient id="waveLight" x1="0" x2="1">
                <stop offset="0%" stopColor="#E7C49D" stopOpacity="0.45" />
                <stop offset="50%" stopColor="#F5B66E" stopOpacity="0.65" />
                <stop offset="100%" stopColor="#FFF3E0" stopOpacity="0.95" />
              </linearGradient>

              <linearGradient id="waveDark" x1="0" x2="1">
                <stop offset="0%" stopColor="#3A0E00" stopOpacity="0.5" />
                <stop offset="50%" stopColor="#E57621" stopOpacity="0.75" />
                <stop offset="100%" stopColor="#FCBB65" stopOpacity="0.85" />
              </linearGradient>
            </defs>

            <path
              d="M0,40 C200,80 400,0 720,40 C1040,80 1240,0 1440,40 L1440,0 L0,0 Z"
              className="fill-[url(#waveLight)] dark:fill-[url(#waveDark)]"
            />
          </svg>
        </motion.div>
      </div>

      {/* BLOB DECOR */}
      <div className="absolute inset-0 -z-10 pointer-events-none">
        <motion.div
          animate={{ y: [0, -12, 0] }}
          transition={{
            duration: ANIMATION_CONFIG.BLOB.DURATIONS[0],
            repeat: Infinity,
            ease: "easeInOut",
          }}
          className="absolute left-8 top-10 w-40 h-40 rounded-full bg-primary/10 blur-3xl"
        />
        <motion.div
          animate={{ y: [-10, 10, -10] }}
          transition={{
            duration: ANIMATION_CONFIG.BLOB.DURATIONS[1],
            repeat: Infinity,
            ease: "easeInOut",
          }}
          className="absolute right-10 top-28 w-48 h-48 rounded-full bg-accent/10 blur-3xl"
        />
        <motion.div
          animate={{ y: [8, -8, 8] }}
          transition={{
            duration: ANIMATION_CONFIG.BLOB.DURATIONS[2],
            repeat: Infinity,
            ease: "easeInOut",
          }}
          className="absolute left-1/2 bottom-24 -translate-x-1/2 w-44 h-44 rounded-full bg-secondary/10 blur-3xl"
        />
      </div>

      {/* MAIN CONTENT */}
      <div className="relative z-10 bg-gradient-to-b from-bg-base via-bg-soft to-bg-warm dark:from-bg-base dark:via-bg-soft dark:to-bg-warm">
        <div className="max-w-7xl mx-auto px-6 md:px-10 py-20">
          <motion.div
            variants={ANIMATION_CONFIG.CONTAINER}
            initial="hidden"
            animate={controls}
            className="text-center max-w-3xl mx-auto"
          >
            <motion.h4
              variants={ANIMATION_CONFIG.ITEM}
              className="text-primary font-semibold tracking-wide mb-2"
            >
              Keunggulan Kami
            </motion.h4>

            <motion.h2
              variants={ANIMATION_CONFIG.ITEM}
              className="text-3xl md:text-4xl lg:text-5xl font-extrabold text-text-primary dark:text-text-secondary leading-tight"
            >
              Kenapa Makan{" "}
              <motion.span
                className="text-primary"
                animate={{
                  textShadow: [
                    "0px 0px 0px rgba(229,118,33,0)",
                    "0px 0px 12px rgba(229,118,33,0.35)",
                    "0px 0px 0px rgba(229,118,33,0)",
                  ],
                }}
                transition={{
                  duration: 2.2,
                  repeat: Infinity,
                  ease: "easeInOut",
                }}
              >
                di Gelap Nyawang?
              </motion.span>
            </motion.h2>

            <motion.p
              variants={ANIMATION_CONFIG.ITEM}
              className="mt-4 text-base md:text-lg text-text-muted dark:text-text-secondary/80 leading-relaxed"
            >
              Tiap sudut Gelap Nyawang punya cerita: dari resep keluarga,
              inovasi anak muda Bandung, sampai rasa hangat yang susah dilupain.
            </motion.p>
          </motion.div>

          {/* GRID */}
          <motion.div
            variants={ANIMATION_CONFIG.CONTAINER}
            initial="hidden"
            animate={controls}
            className="mt-12 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-8"
          >
            {CARDS_DATA.map((card) => (
              <AboutCard key={card.id} card={card} isMobile={isMobile} />
            ))}
          </motion.div>
        </div>
      </div>
    </section>
  );
}